import React, { useState } from 'react';
import { AppBar, Toolbar, Typography, Button, IconButton, Hidden, Box, Menu, MenuItem } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import { Link } from 'react-router-dom';
import { useUser } from '../../hooks/UserProvider';
import { Link as ScrollLink } from 'react-scroll';
import Login from '../Login/Login';
import './NavBar.scss';

interface NavbarProps { }

const Navbar = (props: NavbarProps) => {
  const { userName, setUserName } = useUser();
  const [menuAnchor, setMenuAnchor] = useState<null | HTMLElement>(null);
  const [open, setOpen] = useState(false);

  const handleClick = () => {
    setOpen(prevOpen => !prevOpen);
  };

  const handleMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setMenuAnchor(event.currentTarget);
  };

  const handleMenuClose = () => {
    setMenuAnchor(null);
  };

  const handleLogout = () => {
    setUserName(null);
    handleMenuClose();
  };

  return (
    <Box className="navbar">
      <AppBar position="fixed" className="app-bar">
        <Toolbar className="toolbar">
          <Typography variant="h6" component="div">
            <Link to="/" style={{ textDecoration: 'none', color: 'inherit' }}>
              <img src="/images/logo.png" alt="Logo" className="logo" />
            </Link>
          </Typography>

          <Hidden smDown>
            <Box className="menu-items">
              <Button color="inherit" component={ScrollLink} to="about-section" smooth={true} duration={500} offset={-200} spy={true} ></Button>
              <Button color="inherit" component={Link} to={userName ? "/ChoiceTreatment" : "/Auth"}> </Button>
              {userName && (
                <Button color="inherit" component={Link} to="/my-appointments"> </Button>
              )}
              <Button color="inherit" component={ScrollLink} to="contact-section" smooth={true} duration={500} offset={-200} spy={true}> </Button>
            </Box>
          </Hidden>

          <Box className="user-section">
            <IconButton color="inherit">
              <SearchIcon />
            </IconButton>

            {userName ? (
              <>
                <Typography className="user-greeting">, {userName}</Typography>
                <Hidden smDown>
                  <Button color="inherit" onClick={handleLogout}></Button>
                </Hidden>
              </>
            ) : (
              <>
                <Button color="inherit" onClick={handleClick}> / </Button>
                <Login open={open} onClose={handleClick} />
              </>
            )}

            <Hidden smUp>
              <IconButton color="inherit" onClick={handleMenuOpen}>
                <MenuIcon />
              </IconButton>
            </Hidden>
          </Box>
        </Toolbar>
      </AppBar>

      <Menu anchorEl={menuAnchor} open={Boolean(menuAnchor)} onClose={handleMenuClose} sx={{ mt: 6 }}>
        <MenuItem component={Link} to="/about" onClick={handleMenuClose}></MenuItem>
        <MenuItem component={Link} to={userName ? "/ChoiceTreatment" : "/Auth"} onClick={handleMenuClose}> </MenuItem>
        {userName && <MenuItem component={Link} to="/my-appointments" onClick={handleMenuClose}> </MenuItem>}
        <MenuItem component={Link} to="/my-appointments" onClick={handleMenuClose}> </MenuItem>
        {userName && <MenuItem onClick={handleLogout}></MenuItem>}
      </Menu>
    </Box>
  );
};

export default Navbar;
