import React, { useEffect, useRef, useState } from 'react';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin from '@fullcalendar/interaction';
import Tooltip from '@mui/material/Tooltip';
import { toJewishDate, toHebrewJewishDate } from "jewish-date";
import AvailableAppointments from '../AvailableAppointments/AvailableAppointments';
import { Link, useParams } from 'react-router-dom';
import './Calendar.scss';

interface CalenderProps {

}

const Calender = (props: CalenderProps) => {
  const { treatmentId } = useParams<{ treatmentId: string }>();
  const calendarRef = useRef<FullCalendar>(null);

  const [selectedDate, setSelectedDate] = useState<string>()

  const getJewishDateString = (date: Date) => {
    const jewishDate = toJewishDate(date);
    return toHebrewJewishDate(jewishDate);
  };

  const handleDatesSet = (info: any) => {
    info.view.el.querySelectorAll('.fc-daygrid-day').forEach((dayCell: any) => {
      const dateStr = dayCell.getAttribute('data-date');
      if (dateStr) {
        const date = new Date(dateStr);
        const jewishDateStr = getJewishDateString(date);
        dayCell.querySelector('.fc-daygrid-day-number').innerHTML += ` <span class="${jewishDateStr.day.length > 2 ? 'hebrew-date' : 'hebrew-date-short'}">${jewishDateStr.day}</span>`;

      }
    });
  }
  const updateCalendarTitle = (info: any) => {
    const toolbarTitle = info.view.el.querySelector('.fc-toolbar-title');
    if (toolbarTitle) {
      const dayCells = info.view.el.querySelectorAll('.fc-daygrid-day');
      if (dayCells.length > 0) {
        const middleDayCell = dayCells[Math.floor(dayCells.length / 2)];
        const dateStr = middleDayCell.getAttribute('data-date');
        const date = new Date(dateStr);

        const firstJewishDateStr = getJewishDateString(new Date(date.getFullYear(), date.getMonth(), 1));
        const lastJewishDateStr = getJewishDateString(new Date(date.getFullYear(), date.getMonth() + 1, 0));

        const jewishMonthDisplay = firstJewishDateStr.monthName !== lastJewishDateStr.monthName
          ? `${firstJewishDateStr.monthName} - ${lastJewishDateStr.monthName}`
          : firstJewishDateStr.monthName;

        toolbarTitle.innerHTML = `${date.toLocaleString('default', { month: 'long' })} ${date.getFullYear()} <span class="hebrew-month-year"> | ${jewishMonthDisplay} ${firstJewishDateStr.year}</span>`;
      }
    }
  };

  const today = new Date()
  const nextWeek = new Date(today)
  nextWeek.setDate(today.getDate() + 7)

  const availableDates = [];
  let date = new Date(today);

  while (date <= nextWeek) {
    if (date.getDay() !== 6) {
      availableDates.push({
        start: date.toISOString().split('T')[0],
        display: 'background',
        backgroundColor: '#A4E7A4', //     
      });
    }
    date.setDate(date.getDate() + 1);
  }

  const isSelectable = (date: Date) => {
    date.setHours(0, 0, 0, 0)
    today.setHours(0, 0, 0, 0)
    return date >= today && date <= nextWeek && date.getDay() !== 6;
  };

  const selectedDateRef = useRef<string | null>(null);

  const handleDateClick = (arg: any) => {
    const date = new Date(arg.dateStr);
    if (isSelectable(date)) {
      setSelectedDate(arg.dateStr)
      // if (selectedDateRef.current !== arg.dateStr) {
      //   selectedDateRef.current = arg.dateStr;
      // }
    } else {
      alert('    ');
    }
  };


  useEffect(() => {
    const initialize = async () => {
      if (calendarRef.current) {
        const calendarApi = calendarRef.current.getApi();
        await handleDatesSet({ view: calendarApi });
      }
    };
    initialize();
  }, []);

  useEffect(() => {
    const initialize = async () => {
      if (calendarRef.current) {
        const calendarApi = calendarRef.current.getApi();
        await updateCalendarTitle({ view: calendarApi });
      }
    };
    initialize();
  }, [selectedDate]);

  return (
    <div
      style={{
        backgroundColor: '#E1CECD',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        height: '100vh',
        width: '100vw',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-start',
        paddingRight: '3%',
        paddingTop: '2%'
      }}
    >
      <div
        style={{
          height: '480px',
          padding: '25px',
          borderRadius: '5px',
          marginTop: '30px',
          maxWidth: '500px', //    
          width: '100%', //      
          boxShadow: '0 2px 10px rgba(0, 0, 0, 0.2)' //     
        }}
      >
        <FullCalendar
          ref={calendarRef}
          plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
          initialView="dayGridMonth"
          height="65vh"
          fixedWeekCount={false} //     6     
          initialDate={new Date()} //    
          dateClick={handleDateClick}
          events={availableDates}
          locale="he"
          headerToolbar={{
            left: '',   //     
            center: 'title',   //       
            right: '',  //     
          }}

          dayCellContent={(arg) => {
            const cellDate = new Date(arg.date);
            const isDateSelectable = isSelectable(cellDate);

            return (
              <Tooltip arrow title={isDateSelectable ? '' : '    '}>
                <span>{arg.dayNumberText}</span>
              </Tooltip>

            );
          }}
        />


      </div>
      {selectedDate && (
        <AvailableAppointments selectedDate={selectedDate} treatmentId={treatmentId} />
      )}
    </div>

  );
};

export default Calender;
